.contact {
    width: 100%;
    height: 90vh;
    display: flex;
  }
.contact .leftSide {
  height: 100%;
  flex: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.contact .rightSide {
  height: 100%;
  flex: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contact .rightSide h1 {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-weight: 10;
  font-size: 50px;
}
form {
  display: flex;
  flex-direction: column;
  width: auto;
  height: auto;
  padding: 60px;
}
form input {
  height: 40px;
  width: 80%;
  border: none;
  border-bottom: 1px solid #121619;
  color: black;
}
form textarea {
  margin-top: 15px;
  height: 70px;
  width: 80%;
  border: none;
  border-bottom: 1px solid #121619;
  color: black;
}
input::placeholder,
textarea::placeholder {
  font-weight: bold;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}
form input:focus,
textarea:focus {
  outline: none;
}
form label {
  margin-top: 15px;
  color: grey;
}
form button {
  margin-top: 40px;
  border-radius: 10px;
  width: 140px;
  height: 40px;
  border: none;
  background-color: #121619;
  color: whitesmoke;
  font-size: 15px;
  cursor: pointer;
}

@media (max-width: 850px){
  form {
    padding: 30px;
  }
}

@media (max-width: 560px){
  .contact .leftSide{
    display: none;
  }
  .contact .rightSide h1{
    margin: 0;
  }
  #contact-form {
    margin: 0 30px;
  }
  form input, form textarea{
    width: 100%;
  }
}